import { Form } from './Form';
import { ensureAllElements } from '../../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._payment = ensureAllElements('.button_alt', container);
        if (this._payment) {
            this._payment.forEach((button) => {
                button.addEventListener('click', () => {
                    this.paymentSelected = button.name;
                });
            });
        }
    }
    set paymentSelected(name) {
        this._payment.forEach((button) => {
            this.toggleClass(button, 'button_alt-active', button.name === name);
            this.events.emit(`payment:select`, { payment: name });
        });
    }
    paymentSelectedRemove() {
        this._payment.forEach((button) => {
            if (button.classList.contains('button_alt-active')) {
                button.classList.remove('button_alt-active');
            }
        });
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value =
            value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value =
            value;
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
}
